@import '../App.css';
.about {
    height: auto;
}
.tall {
    width: fit-content;
    max-height: 625px;
}
.about .container {
    gap: 3rem;
    padding: 3%;
}
.about .left {
    display: flex;
    width: auto;
    align-items: center;
}
.about
.left img {
    height: fit-content;
}
.about .right {
    background-color: #fff;
    border-radius: 15px;
    padding: 2%;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.25);
}
.about .right h1 {
    color: var(--darker-green);
}
.about .right {
    gap: 1rem;
}
.right p {
    font-size: 1.25rem;
}
.right .dark-green, .right .olive-green {
    font-weight: 700;
}
@media all and (max-width: 960px) {
    .about .left {
        display: flex;
        justify-content: center;
        
    }
    .about
    .right {
        padding: 5%;
    }
    .about
    .container {
        flex-direction: column;
        text-align: left;
    }
}